.wrapper {
  overflow: hidden;
}
h1,
h2,
h3 {
  .title-text {
    display: inline-block;
    font-size: 16px;
    line-height: 130%;
    font-weight: 400;
    color: #B2B2B2;
  }
}
.h2 {
  font-size: 42px;
  line-height: 130%;
  font-weight: 700;
}
.h3 {
  font-size: 32px;
  line-height: 128%;
  font-weight: 700;
}
.btn-blue {
  background: #5856D2;
  border-radius: 12px;
  &:hover {
    opacity: 0.8;
  }
  span {
    line-height: 130%;
    font-weight: 600;
    color: #fff;
  }
}
.btn-blue__outline {
  background: transparent;
  border: 1.5px solid #5856D2;
  color: #5856D2;
  &:hover {
    background: #5856D2;
    color: #fff;
  }
  span {
    line-height: 130%;
    font-weight: 600;
  }
}
.btn-gray {
  background: #F3F3FF;
  color: #5856D2;
  &:hover {
    background: #D9D9D9;
  }
  span {
    line-height: 130%;
    font-weight: 500;
  }
}
.section-top {
  display: flex;
  align-items: center;
  gap: 15px;
  &__link {
    height: 40px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: 1px solid #5856D2;
    border-radius: 12px;
    padding: 5px 21px;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    color: #5856D2;
    &:hover {
      background: #5856D2;
      color: #fff;
      i svg path {
        stroke: #fff;
      }
    }
    i {
      width: 7px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    width: 7px;
    height: 7px;
    background: #F2EFF9;
    opacity: 1 !important;
    margin: 0 3.5px !important;
  }
  &-bullet-active {
    background: #3F3F3F;
  }
}
.breadcrumbs {
  padding: 10px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    line-height: 130%;
    color: #B2B2B2;
    &--active {
      color: #3f3f3f;
    }
    &:hover {
      color: #3f3f3f;
      i svg path {
        stroke: #3f3f3f;
      }
    }
    span {
      text-decoration: underline;
    }
    i {
      width: 10px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
}
.pagination {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  &__prev,
  &__next {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ECECEC;
    border-radius: 50%;
    &:hover {
      background: #5856D2;
      border-color: #5856D2;
      svg path {
        stroke: #fff;
      }
    }
    svg {
      max-width: 80%;
      max-height: 80%;
    }
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  &__item {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ECECEC;
    border-radius: 50%;
    font-size: 14px;
    line-height: 130%;
    font-weight: 500;
    color: #9F9F9F;
    &--active {
      background: #5856D2;
      border-color: #5856D2;
      color: #fff;
    }
    &:hover {
      background: #5856D2;
      border-color: #5856D2;
      color: #fff;
    }
  }
}
.nice-select {
  z-index: 5;
  width: auto;
  min-width: 96px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ECECEC;
  border-radius: 12px;
  padding: 5px 30px 5px 15px;
  font-size: 14px;
  line-height: 130%;
  white-space: normal;
  &:after {
    top: 50%;
    right: 15px;
    transform: translate(0, -50%);
    width: 11px;
    height: 6px;
    background-image: url("../img/down-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }
  &:hover {
    &:after {
      background-image: url("../img/down-arrow3.svg");
    }
  }
  .current {
    white-space: nowrap;
    overflow: hidden;
  }
  .list {
    z-index: 3;
    width: 100%;
    min-width: 50px;
    background: #fff;
    box-shadow: 0px 15px 35.5px 0px rgba(118, 118, 118, 0.25);
    border-radius: 12px;
    padding: 0 5px;
    li {
      background-color: transparent !important;
      border: none;
      padding: 6px 0;
      font-size: 14px;
      line-height: 125%;
      color: #9F9F9F;
      &:first-child {
        padding-top: 8px;
      }
      &:last-child {
        padding-bottom: 8px;
      }
      &:not(:last-child) {
        border-bottom: 1px solid #ECECEC;
      }
    }
    li:hover {
      color: #3F3F3F;
    }
    .selected {
      font-weight: 500;
      color: #3F3F3F;
    }
  }
}
.open {
  &:after {
    transform: translate(0, -50%) rotate(-180deg) !important;
    background-image: url("../img/down-arrow3.svg");
  }
}
.form {
  &-title {
    text-align: center;
    font-size: 24px;
    line-height: 128%;
    font-weight: 700;
  }
  &-wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
  }
  &-group {
    position: relative;
    &__input {
      position: relative;
    }
    .input-icon {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translate(0, -50%);
      width: 16px;
      img {
        width: 100%;
      }
    }
    input {
      width: 100%;
      height: 55px;
      background: #F8F8FC;
      border: 1px solid transparent;
      border-radius: 12px;
      padding: 0 20px 0 45px;
      font-size: 14px;
      line-height: 128%;
      color: rgba(63, 63, 63, 1);
      &::placeholder {
        color: rgba(63, 63, 63, 0.5);
      }
    }
    textarea {
      width: 100%;
      background: #F8F8FC;
      border: 1px solid transparent;
      border-radius: 12px;
      padding: 20px;
      font-size: 14px;
      line-height: 128%;
      color: rgba(63, 63, 63, 1);
      resize: none;
      &::placeholder {
        color: rgba(63, 63, 63, 0.5);
      }
    }
    .nice-select {
      width: 100%;
      height: 55px;
      justify-content: start;
      background: #F8F8FC !important;
      border: 1px solid transparent;
      border-radius: 12px;
      padding: 0 45px 0 20px;
    }
    .list {
      background: #F8F8FC !important;
    }
  }
  .password-group {
    input {
      padding-right: 45px;
    }
  }
  .password-btn {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 15px;
    transform: translate(0, -50%);
    width: 14px;
    cursor: pointer;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .password-btn--active {
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
  .error {
    input {
      border-color: #EA5252;
    }
  }
  .error-text {
    display: block;
    padding-left: 20px;
    font-size: 14px;
    line-height: 128%;
    color: #EA5252;
    margin-top: 5px;
  }
  .forgot-password {
    text-align: right;
    margin-top: 10px;
    a {
      font-size: 14px;
      line-height: 128%;
      &:hover {
        color: #5856D2;
      }
    }
  }
  &-btn {
    width: 100%;
    height: 55px;
    padding: 5px 15px;
    margin-top: 15px;
    span {
      font-size: 16px;
    }
  }
  &-register {
    margin-top: 15px;
    &__text {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 20px;
      p {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 5px;
        font-size: 14px;
        line-height: 128%;
        color: #9F9F9F;
      }
      p:nth-child(2) {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          width: 100%;
          height: 1px;
          border: 1px solid #ECECEC;
          pointer-events: none;
        }
        span {
          position: relative;
          z-index: 2;
          background: #fff;
          padding: 0 12px;
        }
      }
      p a {
        font-weight: 500;
        text-decoration: underline;
        color: #5856D2;
      }
    }
  }
  &-socials {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-top: 15px;
  }
  &-social {
    max-width: 36px;
    width: 100%;
    img {
      width: 100%;
    }
  }
  &-text {
    text-align: center;
    font-size: 12px;
    line-height: 128%;
    margin-top: 15px;
    a {
      text-decoration: underline;
      color: #5856D2;
    }
  }
  &-text2 {
    text-align: center;
    font-size: 12px;
    line-height: 128%;
    color: #9F9F9F;
    margin-top: 10px;
  }
  &-check {
    position: relative;
    display: flex;
    align-items: center;
    margin-top: 15px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #5856D2;
      }
    }
    label {
      position: relative;
      padding-left: 30px;
      font-size: 12px;
      line-height: 128%;
      &:after {
        content: '';
        position: absolute;
        top: -3px;
        left: 0;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #ECECEC;
        border-radius: 6px;
      }
      a {
        position: relative;
        z-index: 3;
        text-decoration: underline;
        color: #5856D2;
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(63, 63, 63, 0.5);
  padding: 0 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog {
    position: relative;
    max-width: 500px;
    width: 100%;
    background: #ffffff;
    border-radius: 12px;
    padding: 25px 50px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 12px;
    background: transparent;
    padding: 0;
    &:hover {
      svg path {
        stroke: #5856D2;
      }
    }
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
.fancybox__content {
  width: 90% !important;
  height: auto !important;
  max-height: 100% !important;
  img {
    transform: unset !important;
    width: 100% !important;
    height: auto !important;
    max-height: 100% !important;
    object-fit: cover !important;
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 36px;
  }
  .section-top {
    justify-content: space-between;
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 24px;
    .title-text {
      font-size: 14px;
    }
  }
  .h3 {
    font-size: 24px;
  }
  .section-top {
    &__link {
      height: 35px;
      padding: 5px 11px;
      font-size: 12px;
      i {
        width: 6px;
      }
    }
  }
  .pagination {
    justify-content: space-between;
    gap: 5px;
    &__prev,
    &__next {
      width: 54px;
      height: 54px;
      svg {
        width: 24px;
        height: auto;
      }
    }
    &__items {
      gap: 5px;
    }
    &__item {
      width: 24px;
      height: 24px;
      font-size: 11.2px;
    }
  }
  .modal {
    &__dialog {
      padding: 30px 20px;
    }
  }
}

@media screen and (max-width: 359px) {
  .h2 {
    font-size: 20px;
  }
  .h3 {
    font-size: 20px;
  }
  .pagination {
    &__prev,
    &__next {
      width: 32px;
      height: 32px;
      svg {
        width: 14px;
      }
    }
  }
}