.product-main {
  padding: 30px 0 50px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
  }
  .gallery,
  &__img-wrap {
    position: relative;
    width: 45%;
  }
  &__img {
    position: relative;
    display: block;
    padding-bottom: 88%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  &__desc {
    width: 55%;
  }
  .gallery__big {
    .swiper-slide {
      padding-bottom: 81%;
    }
  }
  .gallery__small {
    margin-top: 15px;
    .swiper-slide {
      padding-bottom: 16%;
      cursor: pointer;
      &:hover {
        img {
          border-color: #5856D2;
          opacity: 1;
        }
      }
      img {
        border: 3px solid transparent;
        opacity: 0.5;
        transition: all 0.3s;
      }
    }
    .swiper-slide-thumb-active {
      img {
        border-color: #5856D2;
        opacity: 1;
      }
    }
  }
  .swiper-slide {
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
  }
  .swiper-pagination {
    display: none;
  }
  &__title {
    .show-more__title {
      display: none;
    }
  }
  .show-title {
    position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -7px;
      width: 46px;
      height: 46px;
      background: #fff;
      filter: blur(3px);
      border-radius: 50%;
      pointer-events: none;
    }
    .show-more__title {
      position: absolute;
      z-index: 2;
      right: 0;
      bottom: 0;
      display: inline-block;
      font-size: 20px;
      line-height: 128%;
      font-weight: 600;
      color: #5856D2;
      cursor: pointer;
    }
    .show-more__title--active {
      display: none;
    }
  }
  .show-title--active {
    display: block;
    overflow: unset;
    &:after {
      display: none;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    margin-top: 15px;
  }
  .work-card__feedback {
    width: auto;
    justify-content: start;
    gap: 14px;
  }
  .work-card__feedback-text {
    font-size: 16px;
  }
  .work-card__feedback-star {
    i {
      width: 20px;
      img {
        margin-top: -5px;
      }
    }
    span {
      font-size: 14px;
    }
    .number-feedback {
      color: #9F9F9F;
    }
  }
  &__subgroup {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__number {
    font-size: 16px;
    line-height: 128%;
    color: #9F9F9F;
  }
  &__views {
    display: flex;
    align-items: center;
    gap: 5px;
    i {
      width: 14px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 128%;
      color: #9F9F9F;
    }
  }
  .work-card__price-wrap {
    gap: 5px 15px;
    margin-top: 15px;
  }
  .work-card__price-news {
    font-size: 36px;
    span {
      font-weight: 600;
    }
  }
  .work-card__price-old {
    font-size: 20px;
  }
  .work-card__discount {
    font-size: 20px;
  }
  &__checkboxes-wrap {
    margin-top: 25px;
  }
  &__checkboxes-title {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 16px;
    line-height: 128%;
    font-weight: 500;
    color: #9F9F9F;
  }
  &__checkboxes-colors {
    span {
      color: #3F3F3F;
    }
    .tab__content2 {
      display: none;
    }
    .tab__content2--active {
      display: inline-block;
    }
  }
  &__checkboxes {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    margin-top: 12px;
  }
  .checkboxes-size {
    .product-main__checkbox {
      input:checked ~ label {
        border: 3px solid #5856D2;
      }
      label {
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-align: center;
        background: transparent;
        border: 1px solid #ECECEC;
        font-size: 16px;
        line-height: 128%;
        font-weight: 500;
        span:nth-child(2) {
          color: #9F9F9F;
        }
      }
    }
  }
  .checkboxes-color {
    .product-main__checkbox {
      input:checked ~ label {
        border: 3px solid #5856D2;
      }
      label {
        background: #ECECEC;
        border: 1px solid transparent;
      }
    }
  }
  &__checkbox {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    label {
      width: 60px;
      height: 60px;
      display: flex;
      border-radius: 10px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 25px;
  }
  &__btn {
    min-width: 232px;
    height: 60px;
    padding: 5px 15px;
    span {
      font-size: 18px;
    }
    span:nth-child(2) {
      display: none;
    }
  }
  &__btn--active {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
    }
  }
  &__favorite,
  &__send {
    width: 60px;
    height: 60px;
    background: #F8F8FC;
    border-radius: 9px;
    padding: 5px;
    svg {
      max-width: 27px;
      height: auto;
    }
  }
  .favorite--active {
    svg path {
      fill: #5856D2;
    }
  }
  &__list-wrap {
    margin-top: 25px;
    .tab__content2 {
      display: none;
    }
    .tab__content2--active {
      display: block;
    }
  }
  &__tabs {
    display: none;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    li:nth-child(5) ~ li {
      display: none;
    }
    li {
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 10px;
      border-bottom: 1px solid #ECECEC;
      padding-bottom: 10px;
    }
    li span {
      font-size: 14px;
      line-height: 128%;
      font-weight: 500;
    }
    li span:nth-child(1) {
      color: #9F9F9F;
    }
    li span:nth-child(2) {
      max-width: 185px;
      width: 100%;
    }
  }
  &__list--active {
    li:nth-child(5) ~ li {
      display: flex;
    }
  }
  &__btns2 {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 10px;
    margin-top: 10px;
    .show-more--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
  &__list-btn {
    font-size: 14px;
    line-height: 128%;
    font-weight: 500;
    cursor: pointer;
    span:nth-child(2) {
      display: none;
    }
  }
  &__link {
    font-size: 14px;
    line-height: 128%;
    font-weight: 500;
    text-decoration: underline;
    color: #5856D2;
  }
  &__group2 {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    padding: 15px;
  }
  .discount,
  .delivery,
  .hit,
  .professional,
  .sale {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
    padding: 7px 12px;
    font-size: 13px;
    line-height: 127%;
    letter-spacing: 0.2px;
    color: #fff;
  }
  .discount {
    background: #D25683;
  }
  .delivery {
    background: #55B070;
  }
  .hit {
    background: #DBAE1C;
  }
  .professional {
    background: #5856D2;
  }
  .sale {
    background: #EA5252;
  }
}
.works3 {
  padding: 50px 0;
  .section-top {
    justify-content: space-between;
  }
  &__slider {
    position: relative;
    margin-top: 20px;
  }
  .mySwiper {
    overflow: unset;
  }
  .work-card {
    &__title {
      min-height: 41px;
      font-size: 16px;
    }
    &__price-news {
      font-size: 18px;
    }
    &__price-old {
      font-size: 12px;
    }
    &__discount {
      font-size: 12px;
    }
  }
  &-pagination {
    display: none;
  }
}
.works-info {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__left {
    width: calc(100% - 580px);
  }
  &__right {
    width: 530px;
    border: 1px solid #ECECEC;
    border-radius: 12px;
    padding: 30px;
  }
  &__title {
    font-size: 20px;
    line-height: 128%;
    font-weight: 700;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 12px;
    p {
      font-size: 16px;
      line-height: 128%;
      color: #9F9F9F;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 25px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 8px;
      border-bottom: 1px solid #ECECEC;
      padding-bottom: 15px;
      &:last-child {
        border: unset;
        padding: 0;
      }
      span {
        font-size: 16px;
        line-height: 128%;
        font-weight: 500;
      }
      span:nth-child(2) {}
      .color-gray {
        color: #9F9F9F;
      }
      .price {
        height: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #EDEDFA;
        border-radius: 36px;
        padding: 5px 18px;
        font-size: 13px;
      }
      .text-underline {
        text-decoration: underline;
      }
      .edit {
        display: flex;
        align-items: center;
        gap: 5px;
        i {
          width: 10px;
          flex-shrink: 0;
          display: flex;
          align-items: center;
          img {
            width: 100%;
          }
        }
      }
    }
  }
}
.author {
  padding: 50px 0;
  &__inner {
    display: flex;
    align-items: center;
    gap: 70px;
    background: #F8F8FC;
    border-radius: 10px;
    padding: 60px 50px;
  }
  &__left {
    width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__right {
    max-width: 865px;
    width: calc(100% - 350px);
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 12px;
    p {
      font-size: 16px;
      line-height: 128%;
      color: #9F9F9F;
    }
  }
  &__img {
    width: 89px;
    height: 89px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EDEDFA;
    border-radius: 50%;
    img {
      max-width: 34px;
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    margin-top: 20px;
  }
  &__name {
    font-size: 20px;
    line-height: 128%;
    font-weight: 600;
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 5px;
    i {
      width: 20px;
      img {
        width: 100%;
        margin-top: -5px;
      }
    }
    span {
      font-size: 14px;
      line-height: 128%;
      font-weight: 700;
    }
    .number-feedback {
      font-weight: 400;
      color: #9F9F9F;
    }
  }
  &__btns {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
  }
  &__btn {
    width: 100%;
    height: 52px;
    gap: 5px;
    border-radius: 12px;
    padding: 5px 20px;
    i {
      width: 15px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
    }
  }
  &__btn2 {
    &:hover {
      background: #5856D2;
      color: #fff;
    }
  }
  .show-more {
    display: none;
  }
}
.feedback {
  padding: 50px 0 100px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 8px;
    border: 1px solid #ECECEC;
    border-radius: 12px;
    padding: 12px 15px;
    i {
      width: 20px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 19px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__group {
    display: block;
    margin-top: 20px;
    .feedback__rating {
      display: none;
    }
  }
  &__select {
    display: flex;
  }
  .nice-select {
    padding: 5px 47px 5px 36px;
    height: 40px;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translate(0, -50%);
      width: 15px;
      height: 15px;
      background-image: url("../img/select-icon.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  .pagination {
    margin-top: 30px;
  }
  &__images {
    display: none;
  }
}
.feedback-card {
  border: 1px solid #ECECEC;
  border-radius: 12px;
  padding: 30px;
  &__top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 8px 18px;
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 18px;
    &-img {
      position: relative;
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDEDFA;
      border-radius: 50%;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .not-pictured {
      img {
        width: 26px;
        height: auto;
        border-radius: unset;
      }
    }
    &-name {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 10px;
    &-text {
      font-size: 16px;
      line-height: 128%;
      color: #9F9F9F;
    }
  }
  &__stars {
    display: flex;
    align-items: center;
    gap: 10px;
    svg {
      width: 20px;
      margin-top: -5px;
    }
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 16px;
      line-height: 128%;
    }
  }
  &__images {
    display: flex;
    align-items: stretch;
    gap: 12px;
    margin-top: 30px;
  }
  &__img {
    position: relative;
    max-width: 105px;
    width: 100%;
    img {
      width: 100%;
      border-radius: 7px;
    }
  }
  .show-more {
    display: none;
  }
}

@media screen and (max-width: 1300px) {
  .works3 {
    &__slider {
      padding-bottom: 24px;
    }
    &-pagination {
      bottom: 0 !important;
      display: block;
    }
  }
}

@media screen and (max-width: 1200px) {
  .works-info {
    &__inner {
      gap: 30px;
    }
    &__left {
      width: calc(100% - 480px);
    }
    &__right {
      width: 450px;
    }
  }
  .author {
    &__inner {
      gap: 30px;
    }
    &__left {
      width: 280px;
    }
    &__right {
      width: calc(100% - 310px);
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    padding: 20px 0 40px;
    &__inner {
      flex-direction: column;
      gap: 20px;
    }
    .gallery,
    &__img-wrap {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .works3 {
    padding: 40px 0;
  }
  .works-info {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
  }
  .author {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
      padding: 30px;
    }
    &__left {
      width: 100%;
      flex-direction: row;
      align-items: center;
      gap: 30px;
    }
    &__right {
      width: 100%;
    }
    &__group {
      margin-top: 0;
    }
    &__btns {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
    }
    &__img {
      width: 120px;
      height: 120px;
      flex-shrink: 0;
      img {
        max-width: 44px;
        width: 100%;
      }
    }
    &__desc {
      width: 100%;
    }
  }
  .feedback {
    padding: 40px 0 80px;
  }
}

@media screen and (max-width: 576px) {
  .content--column {
    display: flex;
    flex-direction: column;
    .breadcrumbs {
      order: -3;
    }
    .product-main {
      order: -2;
    }
    .works-info {
      order: -1;
    }
  }
  .product-main {
    padding: 20px 0 15px;
    .gallery {
      padding-bottom: 16px;
    }
    &__desc {
      position: relative;
      padding-top: 36px;
    }
    .gallery__small {
      display: none;
    }
    .swiper-pagination {
      bottom: 0;
      display: block;
      &-bullet-active {
        background: #5856D2;
      }
    }
    &__subgroup {
      position: absolute;
      top: 0;
      left: 0;
    }
    &__group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      margin-top: 15px;
    }
    .work-card__feedback {
      gap: 12px;
    }
    .work-card__feedback-text {
      font-size: 14px;
    }
    .work-card__feedback-star {
      i {
        width: 18px;
      }
      span {
        font-size: 12px;
      }
    }
    .work-card__price-wrap {
      gap: 5px 10px;
    }
    .work-card__price-news {
      font-size: 28px;
    }
    .work-card__price-old {
      font-size: 15px;
    }
    .work-card__discount {
      font-size: 15px;
    }
    &__checkboxes-wrap {
      margin-top: 20px;
    }
    &__overflow {
      width: calc(100% + 40px);
      margin-left: -20px;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 0;
        opacity: 0;
        pointer-events: none;
      }
    }
    &__checkboxes-title {
      font-size: 14px;
    }
    &__checkboxes {
      min-width: 536px;
      flex-wrap: unset;
      gap: 8px;
      padding: 0 20px;
      margin-top: 8px;
    }
    .checkboxes-size {
      .product-main__checkbox {
        label {
          span {
            font-size: 13px;
          }
        }
      }
    }
    &__checkbox {
      label {
        width: 50px;
        height: 50px;
        border-radius: 8px;
      }
    }
    &__list-wrap {
      margin-top: 20px;
      .tab2 {
        width: 100%;
        height: 45px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #F8F8FC;
        border-radius: 10px;
        padding: 5px 10px;
        font-size: 13px;
        line-height: 128%;
        font-weight: 500;
      }
      .tab2--active {
        background: #EDEDFA;
        color: #5856D2;
      }
    }
    &__tabs {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 10px;
    }
    &__list {
      margin-top: 15px;
      li span {
        font-size: 13px;
      }
      li span:nth-child(2) {
        max-width: 170px;
      }
    }
    &__text {
      margin-top: 15px;
      h5 {
        font-size: 16px;
        line-height: 128%;
        font-weight: 700;
        margin-bottom: 15px;
      }
      p {
        font-size: 14px;
        line-height: 128%;
      }
    }
    &__btns2 {
      flex-direction: column;
      gap: 15px;
    }
    &__list-btn {
      font-size: 14px;
    }
    &__link {
      display: none;
    }
    &__btns {
      position: fixed;
      z-index: 6;
      left: 0;
      bottom: 83px;
      width: 100%;
      padding: 0 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: calc(100% + 40px);
        background: #E7E7E7;
        filter: blur(27px);
        pointer-events: none;
      }
    }
    &__btn {
      position: relative;
      z-index: 2;
      min-width: unset;
      height: 45px;
      flex-grow: 1;
      span {
        font-size: 14px;
      }
    }
    &__favorite,
    &__send {
      position: relative;
      z-index: 2;
      width: 45px;
      height: 45px;
      background: #fff;
      svg {
        max-width: 22px;
      }
    }
  }
  .works3 {
    padding: 25px 0;
    .work-card {
      &__title {
        min-height: 37px;
        font-size: 14px;
      }
      &__price-news {
        font-size: 16px;
      }
      &__price-old {
        font-size: 10px;
      }
      &__discount {
        font-size: 10px;
      }
    }
  }
  .works-info {
    padding: 15px 0 25px;
    &__left {
      display: none;
    }
    &__right {
      padding: 15px;
    }
    &__list {
      gap: 10px;
      li {
        padding-bottom: 10px;
        span {
          font-size: 14px;
        }
      }
    }
  }
  .author {
    padding: 25px 0;
    &__inner {
      gap: 20px;
      padding: 20px;
    }
    &__left {
      align-items: start;
      gap: 15px;
    }
    &__img {
      width: 60px;
      height: 60px;
      img {
        max-width: 24px;
      }
    }
    &__desc {
      display: flex;
      flex-direction: column;
      padding-top: 10px;
    }
    &__group {
      width: calc(100% + 75px);
      margin-top: 25px;
      margin-left: -75px;
    }
    &__name {
      font-size: 16px;
    }
    &__rating {
      i {
        width: 16px;
      }
      span {
        font-size: 12px;
      }
    }
    &__btns {
      display: flex;
      gap: 10px;
      order: -1;
      margin-top: 0;
    }
    &__btn {
      width: auto;
      height: 40px;
      &:first-child {
        width: 40px;
        padding: 5px;
        span {
          display: none;
        }
      }
      i {
        width: 13px;
      }
      span {
        font-size: 12px;
      }
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    .text-overflow {
      p:last-child {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp:6;
        -webkit-box-orient: vertical;
      }
    }
    .text-overflow--active {
      p:last-child {
        display: grid;
        overflow: unset;
      }
    }
    .show-more {
      display: inline-block;
      font-size: 14px;
      line-height: 120%;
      text-decoration: underline;
      color: #5856D2;
      margin-top: 15px;
      span:nth-child(2) {
        display: none;
      }
    }
    .show-more--active {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
  .feedback {
    padding: 25px 0 50px;
    &__top {
      .feedback__rating {
        display: none;
      }
    }
    &__images {
      display: grid;
      grid-template-columns: repeat(5, calc(20% - 28px / 5));
      gap: 7px;
      margin-top: 15px;
    }
    &__img {
      position: relative;
      &:last-child {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(255, 255, 255, 0.70);
          border-radius: 7px;
        }
        span {
          position: absolute;
          z-index: 2;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 16px;
          line-height: 120%;
          font-weight: 700;
        }
      }
      img {
        width: 100%;
        border-radius: 7px;
      }
    }
    &__group {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 14px;
      margin-top: 15px;
      .feedback__rating {
        display: flex;
        padding: 10px 12px;
        span {
          font-size: 16px;
        }
      }
    }
    &__select {
      max-width: 100%;
      flex-grow: 1;
    }
    .nice-select {
      width: 100%;
    }
    &__cards {
      gap: 10px;
      margin-top: 15px;
    }
    .pagination {
      margin-top: 15px;
    }
  }
  .feedback-card {
    padding: 15px;
    &--height {
      .text-overflow {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 6;
        -webkit-box-orient: vertical;
      }
      .text-overflow--active {
        display: grid;
        overflow: unset;
      }
      .show-more {
        display: inline-block;
        font-size: 14px;
        line-height: 120%;
        text-decoration: underline;
        color: #5856D2;
        margin-top: 8px;
        span:nth-child(2) {
          display: none;
        }
      }
      .show-more--active {
        span:nth-child(1) {
          display: none;
        }
        span:nth-child(2) {
          display: block;
        }
      }
    }
    &__top {
      flex-direction: column;
      align-items: start;
      gap: 15px;
    }
    &__rating {
      order: -1;
      gap: 5px;
      &-text {
        font-size: 12px;
      }
    }
    &__stars {
      order: -1;
      gap: 5px;
      svg {
        width: 16px;
      }
    }
    &__user {
      gap: 12px;
      &-img {
        width: 40px;
        height: 40px;
      }
      .not-pictured {
        img {
          width: 14px;
        }
      }
      &-name {
        font-size: 13px;
      }
    }
    &__text {
      margin-top: 15px;
      p {
        font-size: 14px;
      }
    }
    &__images {
      display: none;
    }
  }
}

