.contests {
  padding: 15px 0 100px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    background: #F8F8FC;
    border-radius: 12px;
    padding: 28px 50px;
    .contests__btn2 {
      display: none;
    }
  }
  &__title {
    max-width: 450px;
    font-size: 28px;
    line-height: 128%;
    font-weight: 700;
  }
  .timer {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 20px;
    &__title {
      font-size: 16px;
      line-height: 128%;
      font-weight: 500;
    }
    &__items {
      display: flex;
      align-items: center;
      gap: 10px;
    }
    &__item {
      position: relative;
      font-size: 28px;
      line-height: 128%;
      font-weight: 700;
      &:last-child {
        &:before {
          display: none;
        }
      }
      &:after {
        content: attr(data-title);
        display: inline-block;
        font-size: 14px;
        line-height: 128%;
        font-weight: 500;
        color: #9F9F9F;
        margin-left: 4px;
      }
      &:before {
        content: ':';
        position: absolute;
        top: 50%;
        right: -12px;
        transform: translate(0, -50%);
        font-size: 14px;
        line-height: 128%;
        font-weight: 500;
      }
    }
    &__seconds {
      min-width: 65px;
    }
  }
  &__btns {
    flex-shrink: 0;
  }
  &__btn {
    height: 40px;
    gap: 5px;
    border-radius: 56px;
    padding: 5px 19px;
    &:hover {
      svg path {
        fill: #fff;
      }
    }
    i {
      width: 14px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 13px;
    }
  }
  &__btn2 {
    width: 100%;
    height: 52px;
    gap: 9px;
    border-radius: 12px;
    padding: 5px 19px;
    i {
      width: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        width: 100%;
        height: 100%;
      }
    }
    span {
      font-size: 16px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
    margin-top: 20px;
  }
  &__sidebar {
    width: 250px;
    .contests__btn {
      display: none;
    }
    .contests__btn2 {
      margin-top: 16px;
    }
  }
  &__right {
    width: calc(100% - 280px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 48px / 4));
    gap: 16px;
  }
  &__cards2 {
    align-items: start;
  }
  &__cards-group {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__tabs {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__tab {
    height: 52px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 7px;
    background: #F8F8FC;
    border-radius: 12px;
    padding: 5px 19px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      i {
        svg path {
          fill: #5856D2;
        }
      }
      span {
        color: #5856D2;
      }
    }
    i {
      max-width: 14px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      svg {
        max-width: 100%;
        max-height: 100%;
      }
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 18px;
      line-height: 128%;
      font-weight: 500;
      transition: all 0.3s;
    }
  }
  .tab2--active {
    background: #EDEDFA;
    i {
      svg path {
        fill: #5856D2;
      }
    }
    span {
      font-weight: 600;
      color: #5856D2;
    }
  }
  .select__wrap {
    display: none;
  }
  .work-card {
    display: flex !important;
    &:hover {
      @media screen and (min-width: 993px) {
        .simple-rating {
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
    &:after {
      display: none;
    }
    &__title {
      min-height: 44px;
      font-size: 17px;
    }
    &__price-news {
      font-size: 19px;
    }
    &__desc {
      padding-top: 15px;
      padding-bottom: 0;
    }
    &__feedback {
      margin-bottom: auto;
      &-text2 {
        font-size: 13px;
        line-height: 128%;
        font-weight: 600;
        color: #9F9F9F;
        span {
          color: #3F3F3F;
        }
      }
    }
    &__feedback2 {
      display: none;
      border: 1px solid #ECECEC;
      border-radius: 12px;
      padding: 10px 15px;
      &--active {
        display: flex;
      }
    }
    .simple-rating {
      position: absolute;
      z-index: 2;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 42px;
      background: #fff;
      margin-top: 12px;
      opacity: 0;
      pointer-events: none;
      &__items {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
      }
      &__item {
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 0;
        margin: 0;
        opacity: 0;
        visibility: hidden;
      }
      &__item:checked ~ .simple-rating__label {
        svg path {
          fill: #EEC231;
        }
      }
      &__label {
        position: relative;
        height: 100%;
        flex: 0 0 10%;
        display: flex;
        align-items: center;
        cursor: pointer;
        color: #F3F3FF;
        svg {
          width: 100%;
          height: auto;
        }
      }
      &__label:hover,
      &__label:hover ~ .simple-rating__label,
      &__label:checked ~ .simple-rating__label:hover {
        svg path {
          fill: #EEC231;
        }
      }
    }
    &__btn2 {
      width: 100%;
      height: 42px;
      border-radius: 12px;
      padding: 5px 10px;
      margin-top: 12px;
      span {
        font-size: 15px;
      }
    }
  }
  .tab__content2 {
    display: none;
    &--active {
      display: block;
    }
  }
  .pagination {
    margin-top: 30px;
  }
}
.contests-info {
  &__title {
    font-size: 20px;
    line-height: 128%;
    font-weight: 600;
    margin-bottom: 10px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    p {
      font-size: 16px;
      line-height: 125%;
      color: #9F9F9F;
    }
  }
}

@media screen and (max-width: 1300px) {
  .contests {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 32px / 3));
      .work-card {
        &:nth-child(18) ~ .work-card {
          display: none !important;
        }
      }
    }
    &__cards-group {
      &:last-child {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contests {
    .timer {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 992px) {
  .contests {
    &__top {
      flex-direction: column;
      text-align: center;
      gap: 20px;
      padding: 25px;
      .contests__btn2 {
        display: flex;
      }
    }
    &__btns {
      display: flex;
      justify-content: center;
      gap: 14px;
    }
    &__btn {
      max-width: 160px;
      height: 52px;
      border-radius: 12px;
    }
    &__btn2 {
      min-width: 260px;
      width: auto;
      flex-grow: 1;
    }
    &__wrap {
      flex-direction: column;
      gap: 20px;
    }
    &__sidebar {
      width: 100%;
      .contests__btn2 {
        display: none;
      }
    }
    &__tabs {
      display: flex;
      align-items: stretch;
      gap: 12px;
    }
    &__tab {
      flex-grow: 1;
      i {
        display: none;
      }
      span {
        font-size: 15px;
      }
    }
    &__right {
      width: 100%;
    }
    .work-card {
      .simple-rating--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .contests {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      .work-card {
        &:nth-child(12) ~ .work-card {
          display: none !important;
        }
      }
    }
    &__cards-group {
      &:nth-child(3) {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contests {
    padding: 20px 0 50px;
    &__top {
      gap: 15px;
      padding: 25px 16px;
      .contests__btns {
        display: none;
      }
    }
    &__title {
      font-size: 22px;
    }
    .timer {
      &__item {
        font-size: 24px;
        &:after {
          font-size: 12px;
        }
        &:before {
          font-size: 12px;
        }
      }
      &__seconds {
        min-width: 55px;
      }
    }
    &__btns {
      width: 100%;
    }
    &__btn,
    &__btn2 {
      max-width: 100%;
      width: 100%;
      height: 42px;
      span {
        font-size: 12px;
      }
    }
    &__btn {
      margin-top: 16px;
    }
    &__sidebar {
      .contests__btn,
      .contests__btn2 {
        display: flex;
      }
      .contests__btn2 {
        margin-top: 0;
      }
    }
    &__tabs {
      display: none;
    }
    &__tab {
      height: auto;
      justify-content: start;
      background: transparent;
      padding: 0;
      &:hover {
        background: transparent;
        i {
          svg path {
            fill: #3F3F3F;
          }
        }
        span {
          color: #3F3F3F;
        }
      }
      span {
        font-size: 14px;
      }
    }
    .tab2--active {
      background: transparent;
    }
    .select__wrap {
      display: block;
      background: #F8F8FC;
      border: 1px solid #ECECEC;
      border-radius: 12px;
      padding: 10px 15px;
      margin-top: 20px;
    }
    .select__wrap--active {
      border-color: #5856D2;
      .select-arrow {
        transform: rotate(-180deg);
        svg path {
          stroke: #5856D2;
        }
      }
      .select__list {
        height: auto;
        margin-top: 15px;
        overflow: unset;
      }
    }
    .select__list-default {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 9px;
    }
    .select__current {
      i {
        svg path {
          fill: #5856D2;
        }
      }
      span {
        font-weight: 600;
        color: #5856D2;
      }
    }
    .select-arrow {
      width: 14px;
      flex-shrink: 0;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .select__list {
      height: 0;
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      transition: all 0.3s;
      overflow: hidden;
      .li--active {
        i {
          svg path {
            fill: #5856D2;
          }
        }
        span {
          font-weight: 600;
          color: #5856D2;
        }
      }
      .tab2--active {
        display: none;
      }
    }
    .work-card {
      &__desc {
        margin-top: 12px;
      }
      &__title {
        min-height: 37px;
        font-size: 14px;
      }
      &__price-news {
        font-size: 16px;
      }
      .simple-rating {
        height: 38px;
      }
      &__btn2 {
        height: 38px;
        span {
          font-size: 12px;
        }
      }
    }
  }
  .contests-info {
    &__title {
      font-size: 16px;
    }
    &__text {
      gap: 15px;
      p {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .contests {
    &__cards {
      grid-template-columns: 100%;
      .work-card {
        &:nth-child(6) ~ .work-card {
          display: none !important;
        }
      }
    }
    &__cards-group {
      &:nth-child(2) {
        display: none;
      }
    }
  }
}