.profile {
  padding: 20px 0 100px;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 40px;
    padding-top: 92px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 200px;
      background: #F8F8FC;
      border-radius: 12px;
      pointer-events: none;
    }
  }
  &__left {
    position: relative;
    z-index: 2;
    width: 280px;
  }
  &__right {
    position: relative;
    z-index: 2;
    width: calc(100% - 320px);
    padding-top: 140px;
  }
  &__title {
    font-size: 32px;
    line-height: 125%;
  }
  &__selects {
    position: relative;
    z-index: 5;
    margin-top: 20px;
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
  }
  &__select {
    width: auto !important;
    height: auto;
  }
  .nice-select {
    min-width: 185px;
  }
  .quantity-select {
    .nice-select {
      min-width: 96px;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 48px / 4));
    gap: 20px 16px;
    margin-top: 20px;
  }
  .work-card {
    &__title {
      min-height: 41px;
      font-size: 16px;
    }
    &__price-news {
      font-size: 18px;
    }
    &__price-old {
      font-size: 12px;
    }
    &__discount {
      font-size: 12px;
    }
  }
  .pagination {
    margin-top: 30px;
  }
  &__user {
    display: flex;
    flex-direction: column;
    align-items: center;
    &-img {
      width: 215px;
      height: 215px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDEDFA;
      border: 20px solid #fff;
      border-radius: 50%;
      margin: 0 auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    .no-photo {
      img {
        max-width: 60%;
        max-height: 60%;
      }
    }
    &-title {
      display: flex;
      align-items: center;
      gap: 6px;
      font-size: 20px;
      line-height: 125%;
      font-weight: 600;
      .star {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        gap: 6px;
        i {
          width: 17px;
          img {
            width: 100%;
          }
        }
        span {
          font-size: 14px;
          line-height: 125%;
          font-weight: 700;
        }
      }
    }
    &-location {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 6px;
      margin-top: 10px;
      i {
        width: 12px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 125%;
        color: #9F9F9F;
      }
    }
    &-socials {
      display: flex;
      justify-content: center;
      gap: 5px;
      margin-top: 18px;
    }
    &-social {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EDEDFA;
      border-radius: 50%;
      &:hover {
        opacity: 0.7;
      }
      img {
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-top: 20px;
    &-item {
      font-size: 16px;
      line-height: 125%;
      font-weight: 500;
      span {
        color: #9F9F9F;
      }
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 20px 15px;
    margin-top: 20px;
  }
  &__btn {
    height: 52px;
    grid-column: 1/3;
    gap: 6px;
    padding: 5px 8px;
    i {
      width: 15px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img,
      svg {
        width: 100%;
        height: auto;
      }
    }
    span {
      font-size: 16px;
    }
  }
  &__add {
    height: 52px;
    background: #EDEDFA;
    border: 1px solid transparent;
    border-radius: 12px;
    padding: 5px 8px;
    color: #5856D2;
    &:hover {
      background: #5856D2;
      border-color: #5856D2;
      color: #fff;
      i svg path {
        stroke: #fff;
      }
    }
    i {
      display: none;
      svg path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 16px;
      line-height: 130%;
      font-weight: 600;
    }
  }
  &__send {
    height: 52px;
    gap: 5px;
    background: transparent;
    border: 1px solid #ECECEC;
    border-radius: 12px;
    padding: 5px 8px;
    color: #9A9AAD;
    &:hover {
      background: #5856D2;
      border-color: #5856D2;
      color: #fff;
      i {
        svg path {
          fill: #fff;
        }
      }
    }
    span {
      font-size: 16px;
    }
    i {
      width: 15px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.3s;
      }
    }
  }
  &__item {
    background: #F8F8FC;
    border-radius: 12px;
    padding: 20px;
    margin-top: 15px;
    &-show {
      display: none;
    }
    @media screen and (min-width: 993px) {
      .swiper-wrapper {
        transform: unset;
        display: grid;
        grid-template-columns: 100%;
        gap: 16px;
      }
    }
    .swiper-slide {
      width: auto !important;
    }
    &-link {
      font-size: 16px;
      line-height: 125%;
      font-weight: 500;
      &:hover {
        color: #5856D2;
      }
      span {
        color: #9F9F9F;
      }
    }
    .active {
      color: #5856D2;
    }
    .border {
      border-top: 1px solid #EDEDED;
      padding-top: 10px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .profile {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 32px / 3));
    }
    .work-card {
      &:nth-child(15) ~ .work-card {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .profile {
    &__inner {
      flex-direction: column;
      gap: 30px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      padding-top: 0;
    }
    &__title {
      font-size: 42px;
    }
    .nice-select {
      height: 46px;
    }
    &__user {
      align-items: start;
      .no-photo {
        img {
          max-width: 52px;
        }
      }
      &-img {
        width: 170px;
        height: 170px;
        border-width: 15px;
        margin: 0 0 0 32px;
      }
      &-desc {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        gap: 15px;
      }
      &-group {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 15px;
      }
      &-location {
        margin-top: 0;
      }
      &-socials {
        margin-top: 0;
      }
    }
    &__info {
      justify-content: start;
      gap: 45px;
    }
    &__btns {
      grid-template-columns: repeat(3, calc(100% / 3 - 24px / 3));
      margin-top: 30px;
    }
    &__btn {
      grid-column: 1;
    }
    @media screen and (min-width: 577px) {
      &__item {
        background: transparent;
        padding: 0;
        margin-top: 25px;
        .swiper {
          overflow: unset;
        }
        &-link {
          height: 52px;
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 4px;
          background: #F8F8FC;
          border: none !important;
          border-radius: 12px;
          padding: 5px 12px !important;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .profile {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
    }
    .work-card {
      display: block !important;
      &:nth-child(12) ~ .work-card {
        display: none !important;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .profile {
    padding: 20px 0 50px;
    &__inner {
      gap: 15px;
      padding-top: 0;
      &:after {
        display: none;
      }
    }
    &__title {
      display: none;
    }
    &__selects {
      margin-top: 0;
    }
    .nice-select {
      min-width: 162px !important;
      height: 40px;
      justify-content: start;
      .list {
        li {
          font-size: 12px;
        }
      }
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
      gap: 20px 10px;
      margin-top: 15px;
    }
    .work-card {
      &__title {
        min-height: 37px;
        font-size: 14px;
      }
      &__price-news {
        font-size: 16px;
      }
      &__price-old {
        font-size: 10px;
      }
      &__discount {
        font-size: 10px;
      }
    }
    .pagination {
      margin-top: 20px;
    }
    &__user {
      display: flex;
      flex-direction: row;
      align-items: start;
      gap: 20px;
      .no-photo {
        img {
          max-width: 32px;
        }
      }
      &-img {
        width: 85px;
        height: 85px;
        flex-shrink: 0;
        border: none;
        margin-left: 0;
      }
      &-desc {
       display: block;
       padding-top: 16px;
      }
      &-group {
        display: block;
      }
      &-location {
        justify-content: start;
        margin-top: 15px;
      }
      &-socials {
        justify-content: start;
        margin-top: 30px;
        margin-left: -30px;
      }
    }
    &__info {
      position: relative;
      justify-content: center;
      gap: 50px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1px;
        height: 100%;
        background: #ECECEC;
        pointer-events: none;
      }
      &-item {
        font-size: 14px;
      }
    }
    &__btns {
      display: flex;
      gap: 10px;
      margin-top: 20px;
    }
    &__btn {
      height: 40px;
      flex-grow: 1;
      border-radius: 10px;
      span {
        font-size: 12.5px;
      }
    }
    &__add {
      width: 40px;
      height: 40px;
      border-radius: 10px;
      span {
        display: none;
      }
      i {
        width: 17px;
        display: flex;
        align-items: center;
      }
    }
    &__send {
      width: 40px;
      height: 40px;
      border-radius: 10px;
      span {
        display: none;
      }
    }
    &__item {
      padding: 0;
      &-show {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 8px;
        border: 1px solid #ECECEC;
        border-radius: 12px;
        padding: 10px 15px;
        cursor: pointer;
      }
      .item-show--active {
        border-color: #5856D2;
        border-bottom: unset;
        border-radius: 12px 12px 0 0;
        .profile__item-btn {
          transform: rotate(-180deg);
        }
        & ~ .item-hidden {
          border-color: #5856D2;
        }
      }
      &-title {
        font-size: 14px;
        line-height: 125%;
        font-weight: 600;
        color: #5856D2;
      }
      &-btn {
        width: 12px;
        flex-shrink: 0;
        transition: all 0.3s;
        img {
          width: 100%;
        }
      }
      &-hidden {
        display: none;
        border: 1px solid #ECECEC;
        border-top: unset;
        border-radius: 0 0 12px 12px;
        padding: 5px 15px 15px 15px;
      }
      .item-hidden--active {
        display: block;
      }
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: 100%;
        gap: 16px;
      }
      &-link {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 359px) {
  .profile {
    &__selects {
      grid-template-columns: 100%;
    }
    &__cards {
      grid-template-columns: 100%;
    }
    .work-card {
      display: block !important;
      &:nth-child(6) ~ .work-card {
        display: none !important;
      }
      &__title {
        min-height: unset;
      }
    }
    &__user {
      flex-direction: column;
      align-items: center;
      &-img {
        margin: 0 auto;
      }
      &-desc {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0;
        padding-top: 0;
      }
      &-location {
        justify-content: center;
      }
      &-socials {
        margin-left: 0;
        margin-top: 15px;
      }
    }
  }
}
