.basket {
  padding-bottom: 125px;
  &__inner {
    margin-top: 10px;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
  }
  &__left {
    width: calc(100% - 350px);
  }
  &__right {
    width: 330px;
  }
  &__group,
  &__group--new {
    background: #fff;
    border-radius: 12px;
    padding: 25px;
  }
  &__group--address {
    margin-top: 20px;
  }
  &__group--form {
    display: none;
    border: 1px solid transparent;
    margin-top: 20px;
    .form {
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      margin-top: 20px;
    }
    .form-group {
      display: flex;
      align-items: center;
      gap: 20px;
      .input-text {
        width: 180px;
        flex-shrink: 0;
        font-size: 16px;
        line-height: 100%;
        font-weight: 500;
      }
      input {
        max-width: 477px;
        padding-left: 20px;
        font-size: 16px;
      }
      .nice-select {
        font-size: 16px;
        color: rgba(63, 63, 63, 0.5);
      }
      .open {
        z-index: 10;
      }
      .nice-select--active {
        color: #3f3f3f;
      }
    }
    .form-select {
      max-width: 477px;
      width: 100%;
    }
    .basket__subgroup {
      margin-top: 30px;
    }
    .item-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      cursor: pointer;
      &:hover {
        .item-arrow {
          svg path {
            stroke: #5856D2;
          }
        }
      }
    }
    .item-show--active {
      .item-arrow {
        svg {
          transform: rotate(-180deg);
        }
        svg path {
          stroke: #5856D2;
        }
      }
    }
    .item-title {
      font-size: 16px;
      line-height: 100%;
      font-weight: 500;
    }
    .item-arrow {
      width: 12px;
      flex-shrink: 0;
      svg {
        width: 100%;
        transition: all 0.3s;
      }
    }
    .item-hidden {
      display: none;
      margin-top: 15px;
      textarea {
        height: 148px;
        font-size: 16px;
      }
    }

  }
  .checked-group {
    border: 1px solid #5856D2;
  }
  .remove-group {
    display: none;
  }
  .basket__group--new {
    display: none;
  }
  .add-group {
    display: block;
  }
  &__btns {
    display: none;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
    &--active {
      display: flex;
    }
  }
  .save {
    height: 55px;
    padding: 5px 45px;
    span {
      font-size: 16px;
    }
  }
  .cancel {
    background: transparent;
    padding: 0;
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
  }
  .new-address__delete {
    font-size: 14px;
    line-height: 100%;
    font-weight: 500;
    color: #9F9F9F;
    cursor: pointer;
  }
  .new-address__add {
    font-size: 14px;
    line-height: 100%;
    color: #5856D2;
    cursor: pointer;
    &--remove {
      display: none;
    }
  }
  .edit {
    font-size: 14px;
    line-height: 100%;
    text-decoration: underline;
    color: #5856D2;
    &:hover {
      text-decoration: unset;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  &__item {
    &-icon {
      width: 16px;
      img {
        width: 100%;
      }
    }
    &-group {
      display: inline-grid;
      grid-template-columns: 100%;
      gap: 10px;
    }
  }
  &__item--flex {
    display: flex;
    align-items: start;
    gap: 8px;
  }
  &__location,
  &__index,
  &__name,
  &__phone {
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
  }
  &__phone:hover {
    color: #5856D2;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    margin-bottom: 15px;
  }
  .into-basket,
  &__delete {
    background: transparent;
    padding: 0;
    font-size: 14px;
    line-height: 100%;
    text-decoration: underline;
    color: #5856D2;
    &:hover {
      text-decoration: unset;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 20px;
  }
  &__subgroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    &-left {
      display: flex;
      align-items: center;
      gap: 30px;
    }
    &-right {
      flex-shrink: 0;
      display: flex;
      align-items: center;
      gap: 16px;
    }
    .form-check {
      margin-top: 0;
      label {
        font-size: 14px;
      }
    }
  }
  &__images {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }
  &__img {
    position: relative;
    max-width: 76px;
    width: 100%;
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__img--last {
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.66);
      border-radius: 12px;
      pointer-events: none;
    }
  }
  &__img-text {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 24px;
    line-height: 100%;
    font-weight: 500;
    color: #fff;
  }
  &__btn2 {
    height: 55px;
    border-radius: 12px;
    padding: 5px 30px;
    span {
      font-size: 15px;
    }
  }
  &-address__btn {
    display: inline-block;
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
    color: #5856D2;
    margin-top: 10px;
    cursor: pointer;
  }
  &__title {
    flex-shrink: 0;
    font-size: 24px;
    .title-text {
      font-size: 14px;
    }
  }
  &__group2 {
    background-image: url("../img/group-bg.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
    border-radius: 12px 12px 0 0;
    padding: 25px 25px 30px 25px;
    .form-check {
      label {
        &:after {
          top: 0;
        }
      }
    }
  }
  &__products,
  &__discounts,
  &__total-price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 19px;
  }
  &__discounts {
    margin-top: 20px;
  }
  &__total-price {
    margin-top: 20px;
  }
  .products-number,
  .products-price,
  .discounts-text,
  .discounts-price {
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
  }
  .discounts-price {
    color: #EA5252;
  }
  .total-price__text {
    font-size: 20px;
    line-height: 100%;
    font-weight: 600;
  }
  .total-price {
    font-size: 20px;
    line-height: 100%;
    font-weight: 600;
    color: #5856D2;
  }
  &__btn {
    width: 100%;
    height: 55px;
    padding: 5px 15px;
    margin-top: 20px;
    span {
      font-size: 16px;
    }
  }
  .promocode {
    position: relative;
    display: flex;
    align-items: center;
    &-wrap {
      background: #fff;
      border-radius: 12px;
      margin-top: 20px;
      .item-show--active {
        .promocode__arrow {
          svg {
            transform: rotate(-180deg);
          }
          svg path {
            stroke: #5856D2;
          }
        }
      }
    }
    &__show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding: 20px 25px;
      cursor: pointer;
      &:hover {
        .promocode__arrow {
          svg path {
            stroke: #5856D2;
          }
        }
      }
    }
    &__title {
      display: flex;
      align-items: center;
      gap: 10px;
      i {
        width: 17px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 100%;
        font-weight: 600;
      }
    }
    &__arrow {
      width: 12px;
      flex-shrink: 0;
      svg {
        width: 100%;
        transition: all 0.3s;
      }
    }
    &__hidden {
      display: none;
      padding: 0 25px 25px 25px;
    }
    &__input {
      height: 45px;
      padding: 0 50px 0 20px;
      font-size: 16px;
      color: #3f3f3f;
    }
    &__btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 45px;
      height: 45px;
      background: #34C785;
      border-radius: 12px;
    }
    &__activated {
      height: 45px;
      display: none;
      align-items: center;
      background: #34C785;
      border-radius: 12px;
      padding: 5px 20px;
      font-size: 16px;
      line-height: 100%;
      color: #fff;
      &--active {
        display: flex;
      }
    }
  }
}
.basket-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  &--active {
    .basket-card__img {
      &:after {
        opacity: 1;
      }
    }
  }
  &--remove {
    display: none;
  }
  &:first-child {
    padding-top: 0;
  }
  &:last-child {
    padding-bottom: 0;
  }
  &:not(:last-child) {
    border-bottom: 1px solid #ECECEC;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__check {
    display: none;
    flex-shrink: 0;
    margin-top: 0;
    margin-right: 10px;
    label {
      width: 20px;
      height: 20px;
      padding: 0;
    }
  }
  &__check--active {
    display: flex;
  }
  &__img {
    position: relative;
    width: 120px;
    flex-shrink: 0;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(88, 86, 210, 0.3);
      border-radius: 12px;
      opacity: 0;
      pointer-events: none;
    }
    img {
      width: 100%;
      border-radius: 12px;
    }
  }
  &__group {
    max-width: 300px;
    margin-left: 20px;
  }
  &__name {
    font-size: 18px;
    line-height: 100%;
    font-weight: 600;
  }
  &__user {
    display: flex;
    align-items: center;
    margin-top: 5px;
    &:hover {
      .basket-card__user-text {
        text-decoration: unset;
      }
    }
    &-icon {
      width: 12px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      margin-right: 6px;
      img {
        width: 100%;
      }
    }
    &-icon2 {
      width: 13px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 100%;
      text-decoration: underline;
      margin-right: 8px;
    }
  }
  &__number {
    font-size: 16px;
    line-height: 100%;
    color: #9F9F9F;
    margin-top: 15px;
  }
  &__right {
    max-width: 400px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__subgroup {
    flex-grow: 1;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .cartcalc {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
    input {
      width: 40px;
      height: 40px;
      text-align: center;
      background: transparent;
      border: none;
      font-size: 16px;
      line-height: 100%;
    }
  }
  .ccalc-minus,
  .ccalc-plus {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F3F3F8;
    border-radius: 8px;
    font-size: 34px;
    line-height: 100%;
    font-weight: 200;
    cursor: pointer;
  }
  &__price-wrap {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__price,
  &__price-one{
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 10px 15px;
  }
  &__price-one {
    font-size: 16px;
    line-height: 100%;
    color: rgba(63, 63, 63, 0.5);
    margin-top: 15px;
  }
  &__new-price {
    font-size: 20px;
    line-height: 100%;
    font-weight: 600;
    color: #5856D2;
  }
  &__old-price {
    font-size: 16px;
    line-height: 100%;
    text-decoration: line-through;
    color: rgba(63, 63, 63, 0.5);
  }
  &__subgroup2 {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__delete {
    width: 33px;
    height: 33px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F3F3F8;
    border-radius: 5px;
    &:hover {
      background: #5856d2;
      svg path {
        fill: #fff;
      }
    }
    svg {
      width: 16px;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
  &__btn {
    height: 50px;
    flex-shrink: 0;
    padding: 5px 33px;
    i {
      width: 21px;
      flex-shrink: 0;
      display: none;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 15px;
    }
  }
}
.favourites-main {
  .basket-card {
    &__right {
      max-width: 750px;
      gap: 20px;
    }
    &__subgroup {
      gap: 20px;
    }
    &__price,
    &__price-wrap {
      justify-content: center;
    }
  }
}

@media screen and (max-width: 1400px) {
  .favourites-main {
    .basket-card {
      &__right {
        max-width: 550px;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .basket {
    &__subgroup {
      &-left {
        gap: 16px;
      }
    }
    &__img:nth-child(2) ~ .basket__img {
      display: none;
    }
    &__img--last {
      display: block !important;
    }
  }
}

@media screen and (max-width: 1200px) {
  .basket {
    &__left {
      width: calc(100% - 300px);
    }
    &__right {
      width: 280px;
    }
  }
  .basket-card {
    &__name {
      font-size: 16px;
    }
    &__right {
      max-width: 250px !important;
    }
    &__subgroup {
      flex-direction: column;
      align-items: start;
      gap: 25px;
    }
    &__price-wrap,
    &__price {
      order: -1;
    }
    &__subgroup2 {
      flex-direction: column;
      align-items: start;
      gap: 8px;
    }
    &__delete {
      width: 40px;
      height: 40px;
      border-radius: 10px;
    }
    &__btn {
      width: 40px;
      height: 40px;
      border-radius: 10px;
      padding: 5px;
      i {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      span {
        display: none;
      }
    }
  }
  .favourites-main {
    .basket-card {
      &__subgroup {
        gap: 25px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .basket {
    padding-bottom: 80px;
    &__wrap {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__group2 {
      padding-bottom: 45px;
      .form-check {
        justify-content: center;
      }
    }
  }
  .basket-card {
    &__img {
      width: 105px;
    }
    &__group {
      max-width: 265px;
    }
    &__user {
      &-text {
        font-size: 12px;
      }
    }
    &__number {
      font-size: 14px;
    }
    &__right {
      max-width: 230px !important;
      gap: 20px;
    }
    &__new-price {
      font-size: 18px;
    }
    &__old-price {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 767px) {
  .basket {
    &__btn2 {
      height: 45px;
      padding: 5px 15px;
      span {
        font-size: 14px;
      }
    }
    &__group--form {
      .form-group {
       flex-direction: column;
        align-items: start;
        gap: 10px;
        .input-text {
          width: 180%;
        }
        input {
          max-width: 100%;
        }
      }
    }
  }
  .basket-card {
    &__img {
      width: 80px;
    }
    &__group {
      max-width: 220px;
      margin-left: 15px;
    }
    &__name {
      font-size: 13px;
    }
    &__number {
      font-size: 12px;
    }
    &__right {
      max-width: 200px !important;
      gap: 15px !important;
    }
    .cartcalc {
      input {
        width: 30px;
        height: 30px;
        font-size: 13px;
      }
    }
    .ccalc-minus,
    .ccalc-plus {
      width: 30px;
      height: 30px;
      font-size: 26px;
    }
    &__price {
      gap: 12px;
    }
    &__new-price {
      font-size: 16px;
    }
    &__old-price {
      font-size: 13px;
    }
    &__price-one {
      font-size: 13px;
      margin-top: 12px;
    }
    &__delete {
      width: 30px;
      height: 30px;
      border-radius: 8px;
      svg {
        width: 14px;
      }
    }
    &__btn {
      width: 30px;
      height: 30px;
      border-radius: 8px;
      i {
        width: 14px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .basket {
    padding-bottom: 50px;
    &__group,
    &__group--new {
      padding: 20px;
    }
    &__subgroup {
      flex-direction: column;
      align-items: start;
      gap: 15px;
      &-left {
        flex-direction: column;
        align-items: start;
        gap: 15px;
      }
      &-right {
        width: 100%;
        flex-wrap: wrap;
        .form-check {
          order: -1;
        }
      }
    }
    &__subgroup--column {
      flex-wrap: wrap;
      flex-direction: row;
      align-items: center;
    }
    &__images {
      gap: 8px;
    }
    &__img {
      width: 55px;
    }
    &__btn2 {
      width: 100%;
    }
    &-address__btn {
      font-size: 14px;
    }
    &__group--form {
      .form {
        gap: 15px;
        margin-top: 15px;
      }
      .form-group {
        .input-text {
          font-size: 12px !important;
        }
        input {
          height: 45px;
          font-size: 14px;
        }
        .nice-select {
          height: 45px;
          font-size: 14px;
        }
      }
      .item-hidden {
        textarea {
          font-size: 14px;
        }
      }
    }
    .save {
      height: 45px;
      padding: 5px 50px;
    }
    .cancel {
      span {
        font-size: 14px;
      }
    }
    &__items {
      margin-top: 10px;
    }
    &__item {
      &-icon {
        width: 14px;
      }
      &-group {
        gap: 8px;
      }
    }
    &__location,
    &__index,
    &__name,
    &__phone {
      font-size: 14px;
    }
    &__subtitle {
      font-size: 14px;
      margin-bottom: 10px;
    }
    &__title {
      font-size: 20px;
    }
    &__group2 {
      padding: 20px 20px 30px 20px;
      .form-check {
        justify-content: start;
      }
    }
    &__products,
    &__discounts,
    &__total-price {
      gap: 15px;
    }
    &__discounts {
      margin-top: 15px;
    }
    &__total-price {
      margin-top: 15px;
    }
    .products-number,
    .products-price,
    .discounts-text,
    .discounts-price {
      font-size: 14px;
    }
    .total-price__text {
      font-size: 16px;
    }
    .total-price {
      font-size: 16px;
    }
    &__btn {
      height: 45px;
      margin-top: 15px;
      span {
        font-size: 14px;
      }
    }
    .promocode {
      &-wrap {
        margin-top: 15px;
      }
      &__show {
        padding: 20px;
      }
      &__hidden {
        display: none;
        padding: 0 20px 20px 20px;
      }
      &__activated {
        height: 45px;
        display: none;
        align-items: center;
        background: #34C785;
        border-radius: 12px;
        padding: 5px 20px;
        font-size: 16px;
        line-height: 100%;
        color: #fff;
        &--active {
          display: flex;
        }
      }
    }
  }
  .basket-card {
    flex-direction: column;
    align-items: start;
    gap: 10px;
    &__img {
      width: 68px;
    }
    &__group {
      max-width: 100%;
    }
    &__name {
      font-size: 13px;
    }
    &__user {
      &-icon {
        width: 10px;
        margin-right: 5px;
      }
      &-icon2 {
        width: 17px;
      }
      &-text {
        font-size: 11px;
      }
    }
    &__number {
      font-size: 11px;
      margin-top: 10px;
    }
    &__right {
      max-width: 100% !important;
      width: 100%;
      align-items: flex-end;
      gap: 5px !important;
      padding-left: 83px;
      &--active {
        padding-left: 113px;
      }
    }
    &__subgroup {
      gap: 10px !important;
    }
    &__subgroup2 {
      flex-direction: row;
      gap: 5px;
    }
  }
}

@media screen and (max-width: 374px) {
  .basket-card {
    &__subgroup2 {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 359px) {
  .basket {
    &__btns {
      gap: 15px;
    }
  }
  .basket-card {
    &__check {
      margin-right: 5px;
    }
    &__img {
      width: 58px;
    }
    &__right {
      gap: 10px;
      padding-left: 73px;
      &--active {
        padding-left: 98px;
      }
    }
    .cartcalc {
      gap: 5px;
    }
    &__price,
    &__price-wrap {
      justify-content: start;
    }
    &__new-price {
      font-size: 14px;
    }
    &__old-price {
      font-size: 12px;
    }
  }
}
