.registration {
  padding: 30px 0 100px;
  &__inner {
    max-width: 500px;
    background: #fff;
    box-shadow: 0px 0px 91.2px 0px rgb(242, 242, 242);
    border-radius: 12px;
    padding: 25px 50px;
    margin: 0 auto;
  }
}

@media screen and (max-width: 576px) {
  .registration {
    padding: 25px 0 50px;
    &__inner {
      padding: 30px 20px;
    }
  }
}